<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        152 
      </span>
    </div>

    <div class="content__catalog">
      <!--  ProductFilter       (  v-bind).
      *    ".sync" .   ProductFilter.vue-->
      <ProductFilter
        :page.sync="page"
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color-id.sync="filterColorsId"
      />

      <section class="catalog">
        <div
          v-if="productsLoading"
        >
          <!--     : -->
           ...
        </div>
        <div
          v-if="productsLoadingFailed"
        >
          <!--       : -->
              !
          <button
            @click.prevent="loadProducts"
          >
              
          </button>
        </div>

        <!--   ProductList     products,
           products,    data ( computed)-->
        <ProductList
          :products="products"
          :filter-color="filterColorsId"
        />
        <!--   BasePagination.- ,   
            ("page"),     v-model.
         ("per-page")   cebab-case,     
          camelCase. -->
        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script> //   -------------------------------------------------------
//    ,      (  
//   ). "@"  "    src"
import axios from 'axios'; // Axios   JavaScript-.   
// HTTP-,          Node.js
import ProductList from '@/components/ProductList.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import BasePagination from '@/components/BasePagination.vue';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductFilter }, //  
  //    (  )   ( )   ,
  //    ( ,   ProductList = ProductList)
  data() { //  (     ,   ).
  //     ,  
    return { //  ()         .
      filterPriceFrom: 0, //     ( : 0)
      filterPriceTo: 0, //     ( : 0)
      filterCategoryId: 0, //  ( : " ")
      filterColorsId: 0, //    ( : " ")
      page: 1, //   ( : )
      productsPerPage: 6, //    (  )
      productsData: null, //     ( : "null")
      categoriesData: 0,
      productsLoading: false, //  
      productsLoadingFailed: false, //     
    };
  },
  computed: { //  :
  //   -  ,    ,  
  // ,      ,      
  // (      ,    )
  //        , ...
    products() {
      return this.productsData //  ProductsData ,   :
        ? this.productsData.items.map((product) => {
          //   map    :
          //    ,        
          const item = { ...product, image: product.image.file.url };
          return item;
        })
        : []; //  ProductsData ,   
    },
    countProducts() { //     
      return this.productsData
        ? this.productsData.pagination.total
        : 0;
    },
  },
  methods: {
    loadProducts() { //  ()     
      this.productsLoading = true; //     
      this.productsLoadingFailed = false; //      
      clearTimeout(this.loadProductsTimer); //    
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get((`${API_BASE_URL}/api/products`), {
            params: { //     (   )
            //    API,  ()   
              page: this.page,
              limit: this.productsPerPage,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              categoryId: this.filterCategoryId,
              colorId: this.filterColorsId,
            },
          })
          .then((response) => this.productsData = response.data) //   
        //    response     productsData
          .catch(() => this.productsLoadingFailed = true) //    ()
          .then(() => this.productsLoading = false); //    
      }, 200); //     ( )
    },
  },
  watch: { // .      
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorsId() {
      this.loadProducts();
    },
  },
  created() { //         
    this.loadProducts();
  },
};
</script>
